<template>  
    <section class="login absolute top-1/2 w-100">
        <div class="w-20 mx-auto my-5">        
            <figure class="flex py-4">
                <img src="@/assets/img/logo-white.png" class="w-4/5 m-auto" alt="logo"/>
            </figure>
            <form class="flex flex-col text-white gap-y-4" >
                <div class="flex flex-col items-start gap-y-1">
                    <label class="pl-2" for="user">Email</label>
                    <input type="text" id="user" name="user" v-model="user"
                    class="bg-transparent border-2 w-full rounded-4xl text-xl px-2">  
                </div>
                <div class="relative flex flex-col items-start gap-y-1">
                    <label class="pl-2" for="pass">Contreña</label>
                    <input id="pass" name="pass" v-model="pass"
                    :type="(showpass) ? 'text' : 'password'" 
                    class="bg-transparent border-2 w-full rounded-4xl text-xl px-2">           
                    <button class="absolute top-2rem right-1rem" type="button" @click="showpass=!showpass" v-show="!showpass"><i class="fas fa-eye-slash"></i></button>
                    <button class="absolute top-2rem right-1rem" type="button" @click="showpass=!showpass" v-show="showpass"><i class="fas fa-eye"></i></button>       
                </div>
                <div class="flex flex-col items-center gap-y-3">
                    <small v-show="is_error" class="text-red py-1">{{error_message}}</small>
                    <router-link class="text-lightBlue underline" :to="{name: 'ForgotPassword'}">Olvide mi contraseña</router-link>
                    <button type="submit"                
                    class="border border-orange bg-orange rounded-3xl py-2 px-16" @click="login">Iniciar sesión</button>
                    <!-- <button type="button" @click="newSubcribe"
                    class="border border-orange bg-transparent rounded-3xl text-orange py-2 px-16">Registrarse</button> -->
                </div>
            </form>        
        </div>
    </section>    
</template>
<script>
import Authorization from "@/services/user/auth.service";
import ClientService from "@/services/user/client.service";

export default {    
    name:'LoginForm',
    data() {
        return {            
            user: '',
            pass: '',
            showpass: false,
            disabledLogin: false,
            is_error: false,
            error_message: 'Mensaje de error'
        }
    },           
    created() {        
        if(Authorization.isClientLogged())
            this.$router.push({name:'Dashboard'})     
    }, 
    methods: {       
        async login(e) {
            e.preventDefault();  
            if(!this.disabledLogin)  {       
                ClientService.login(this.user, this.pass).then(res => {
                    if(res.logged) {
                        // this.$emit('logged', true)
                        this.$router.push({name:'Dashboard'}) 
                        console.log('User is logged!!!')
                    } else {
                        this.error_message = res.message
                        this.is_error = true
                        setTimeout(() => {
                            this.is_error = false                    
                        }, 3000)
                    }
                })
            }
        },        
    }
}
</script>
<style scoped>
.login {
  color: white;  
  background: linear-gradient(to bottom right,var(--blue) 0,var(--dark-blue) 100%);
  margin-top: -200px;
}
</style>
